*{
  margin:0; 
  padding:0; 
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  position:absolute;
  margin:auto; 
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(15px);
  border-radius: 15px;
  padding: 20px;
  height:600px; 
  width: 600px;
  top:0; 
  bottom:0; 
  right:0;
  left:0; 
  justify-content: center;
  box-shadow: 0 8px 20px rgba(0,0,0,0.2);

}
.container h1 {
  color: rgb(88, 82, 82);
  text-align: center;
  justify-content: center;
  margin-bottom: 20px;
  letter-spacing: 2px;
}

.top {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.top input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 8px;
  outline: none;
  font-size: 16px;
}
.add {
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
  user-select: none;
}
.add:hover {
  background-color: #45a049;
}
.list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.todo {
  background-color: rgba(255,255,255,0.9);
  padding: 12px;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.todo button {
  border: none;
  padding: 6px 10px;
  margin-left: 5px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
}
.todo .edit {
  background-color: #ffa500;
  color: rgb(22, 22, 22);
}
.todo .delete {
  background-color: #e74c3c;
  color: rgb(24, 23, 23);
}

.todo button:hover {
  opacity: 0.8;
}
